import { __ } from '@wordpress/i18n';
import { useState, useContext } from '@wordpress/element';
import { AlignmentToolbar, BlockControls, InspectorControls } from '@wordpress/block-editor';
import { PanelBody, PanelRow, RangeControl, Button, ToolbarGroup, ToolbarButton, Dashicon, TextControl, TextareaControl, ToggleControl, TabPanel, __experimentalUnitControl as UnitControl, SelectControl } from '@wordpress/components';

// Variables
import Title from '../../Components/Title';
import BDevice from '../../Components/BDevice';
import Background from '../../Components/Background';
import BColor from '../../Components/BColor';
import BorderControl from '../../Components/BorderControl';
import SpaceControl from '../../Components/SpaceControl';
import ColorsControl from '../../Components/ColorsControl';
import Typography from '../../Components/Typography';
import options from './Const/options';
const { generalStyleTabs, effects, pxUnit, perUnit, emUnit, vhUnit } = options;

import icons from './Const/icons';

import { IndexContext } from './edit';

const Settings = ({ attributes, setAttributes }) => {
    const { slides = [], columns, columnGap, sliderWidth, sliderHeight, isLoop, isAutoplay, speed, effect, isTouchMove, isPage, isPageClickable, isPageDynamic, pageColor, pageWidth, pageHeight, pageBorder, isPrevNext, prevNextColor, sliderAlign, isTitle, titleTypo, isDesc, descTypo, isBtn, linkTarget, btnTypo, btnPadding, btnBorder } = attributes;

    const setActiveIndex = useContext(IndexContext);
    const [device, setDevice] = useState('desktop');

    const addSlide = () => {
        setAttributes({
            slides: [...slides, {
                background: { color: '#00000080' },
                childPositions: [{ 'top': 11, 'left': 5.8 }, { 'top': 22, 'left': 5.8 }, { 'top': 33, 'left': 5.8 }],
                title: `Slide Title ${slides.length + 1}`,
                titleColor: '#fff',
                description: `This content area describes slider ${slides.length + 1} descriptions/details.`,
                descColor: '#fff',
                btnText: `Button ${slides.length + 1}`,
                btnLink: '#',
                btnColors: { color: '#fff', bg: '#4527a4' },
                btnHovColors: { color: '#fff', bg: '#8344c5' }
            }]
        });
        setActiveIndex(slides.length);
    }

    const updateSlide = (index, type, val) => {
        const newSlides = [...slides];
        newSlides[index][type] = val;
        setAttributes({ slides: newSlides });
        setActiveIndex(index);
    }

    return <>
        <InspectorControls>
            <TabPanel className='bPlTabPanel' activeClass='activeTab' tabs={generalStyleTabs}>{tab => <>
                {'general' === tab.name && <>
                    <PanelBody className='bPlPanelBody addRemoveItems' title={__('Add or Remove Slides', 'content-slider-block')}>
                        {slides.map((item, index) => {
                            const { background, title, titleColor, description, descColor, btnText, btnLink, btnColors, btnHovColors } = item;

                            return <PanelBody key={index} className='bPlPanelBody editItem' title={__(`Slide No ${index + 1}:`, 'content-slider-block')} initialOpen={0 !== index ? false : true}>
                                <Background label={__('Background', 'content-slider-block')} background={background} onChange={val => updateSlide(index, 'background', val)} defaults={{ color: '#00000080' }} />

                                <PanelRow>
                                    <Title mt='0' mb='0'>{__('Title:', 'content-slider-block')}</Title>
                                    <TextControl value={title} onChange={val => updateSlide(index, 'title', val)} placeholder={__('Slide title', 'content-slider-block')} />
                                </PanelRow>

                                <BColor label={__('Title Color:', 'content-slider-block')} value={titleColor} onChange={val => updateSlide(index, 'titleColor', val)} defaultColor='#fff' />

                                <Title>{__('Description:', 'content-slider-block')}</Title>
                                <TextareaControl value={description} onChange={val => updateSlide(index, 'description', val)} placeholder={__('Description of the slider', 'content-slider-block')} />

                                <BColor label={__('Description Color:', 'content-slider-block')} value={descColor} onChange={val => updateSlide(index, 'descColor', val)} defaultColor='#fff' />

                                <PanelRow className='mt20'>
                                    <Title mt='0' mb='0'>{__('Button Text:', 'content-slider-block')}</Title>
                                    <TextControl value={btnText} onChange={val => updateSlide(index, 'btnText', val)} placeholder={__('Button label', 'content-slider-block')} />
                                </PanelRow>

                                <PanelRow>
                                    <Title mt='0' mb='0'>{__('Button Link:', 'content-slider-block')}</Title>
                                    <TextControl value={btnLink} onChange={val => updateSlide(index, 'btnLink', val)} placeholder={__('Button link', 'content-slider-block')} />
                                </PanelRow>

                                <ColorsControl label={__('Button Colors:', 'content-slider-block')} colors={btnColors} onChange={val => updateSlide(index, 'btnColors', val)} defaults={{ color: '#fff', bg: '#4527a4' }} />

                                <ColorsControl label={__('Button Hover Colors:', 'content-slider-block')} colors={btnHovColors} onChange={val => updateSlide(index, 'btnHovColors', val)} defaults={{ color: '#fff', bg: '#8344c5' }} />

                                <PanelRow className='itemAction mt20'>
                                    <Button className='removeItem' label={__('Remove', 'content-slider-block')} onClick={e => {
                                        e.preventDefault();
                                        setAttributes({ slides: [...slides.slice(0, index), ...slides.slice(index + 1)] });
                                    }} ><Dashicon icon='no' size={18} />{__('Remove', 'content-slider-block')}</Button>

                                    <Button className='duplicateItem' label={__('Duplicate', 'content-slider-block')} onClick={e => {
                                        e.preventDefault();
                                        setAttributes({ slides: [...slides, { ...slides[index] }] });
                                    }} >{icons.gearSettings}{__('Duplicate', 'content-slider-block')}</Button>
                                </PanelRow>
                            </PanelBody>
                        })}

                        <div className='addItem'>
                            <Button label={__('Add New Slide', 'content-slider-block')} onClick={addSlide} ><Dashicon icon='plus' size={23} />{__('Add New Slide', 'content-slider-block')}</Button>
                        </div>
                    </PanelBody>


                    {/* Layout Settings */}
                    <PanelBody className='bPlPanelBody' title={__('Layout Settings', 'content-slider-block')} initialOpen={false}>
                        <PanelRow>
                            <Title mt='0'>{__('Columns:', 'content-slider-block')}</Title>
                            <BDevice device={device} onChange={val => setDevice(val)} />
                        </PanelRow>
                        <RangeControl value={columns[device]} onChange={val => { setAttributes({ columns: { ...columns, [device]: val } }) }} min={1} max={6} step={1} beforeIcon='grid-view' />

                        <Title>{__('Column Gap:', 'content-slider-block')}</Title>
                        <RangeControl value={columnGap} onChange={val => setAttributes({ columnGap: val })} min={0} max={250} step={1} beforeIcon='arrow-right-alt' />

                        <UnitControl className='mt20' label={__('Width:', 'content-slider-block')} labelPosition='left' value={sliderWidth} onChange={val => setAttributes({ sliderWidth: val })} units={[pxUnit, perUnit, emUnit]} />

                        <UnitControl className='mt20' label={__('Height:', 'content-slider-block')} labelPosition='left' value={sliderHeight} onChange={val => setAttributes({ sliderHeight: val })} units={[pxUnit, emUnit, vhUnit]} />
                    </PanelBody>


                    {/* Slider Options */}
                    <PanelBody className='bPlPanelBody' title={__('Slider Options', 'content-slider-block')} initialOpen={false}>
                        <ToggleControl label={__('Enable Loop', 'content-slider-block')} checked={isLoop} onChange={val => setAttributes({ isLoop: val })} />

                        <ToggleControl label={__('Enable Touch Move', 'content-slider-block')} checked={isTouchMove} onChange={val => setAttributes({ isTouchMove: val })} />

                        <ToggleControl label={__('Enable Autoplay', 'content-slider-block')} checked={isAutoplay} onChange={val => setAttributes({ isAutoplay: val })} />

                        <Title>{__('Speed & Delay (s):', 'content-slider-block')}</Title>
                        <RangeControl value={speed} onChange={val => setAttributes({ speed: val })} min={0} max={10} step={.05} />
                        <small>{__('Smaller speed & delay value will be slide faster', 'content-slider-block')}</small>

                        <PanelRow className='mt20'>
                            <Title mt='0' mb='0'>{__('Effect:', 'content-slider-block')}</Title>
                            <SelectControl value={effect} onChange={val => setAttributes({ effect: val })} options={effects} />
                        </PanelRow>
                        <small>{__('To work fade & creative effects properly, set single column per view', 'content-slider-block')}</small>

                        <Title>{__('Pagination:', 'content-slider-block')}</Title>
                        <ToggleControl label={__('Show Pagination', 'content-slider-block')} checked={isPage} onChange={val => setAttributes({ isPage: val })} />

                        {isPage && <>
                            <ToggleControl label={__('Enable Pagination Clickable', 'content-slider-block')} checked={isPageClickable} onChange={val => setAttributes({ isPageClickable: val })} />

                            <ToggleControl label={__('Enable Pagination Dynamic Bullets', 'content-slider-block')} checked={isPageDynamic} onChange={val => setAttributes({ isPageDynamic: val })} />
                        </>}

                        <Title>{__('Preview Next Button:', 'content-slider-block')}</Title>
                        <ToggleControl label={__('Show Preview Next Button', 'content-slider-block')} checked={isPrevNext} onChange={val => setAttributes({ isPrevNext: val })} />
                    </PanelBody>
                </>}

                {'style' === tab.name && <>
                    {/* Option Style */}
                    {isPage || isPrevNext ? <PanelBody className='bPlPanelBody' title={__('Options Style', 'content-slider-block')}>
                        {isPage && <>
                            <BColor label={__('Pagination Bullets Color:', 'content-slider-block')} value={pageColor} onChange={val => setAttributes({ pageColor: val })} defaultColor='#fff' />

                            <UnitControl className='mt20' label={__('Pagination Width:', 'content-slider-block')} labelPosition='left' value={pageWidth} onChange={val => setAttributes({ pageWidth: val })} units={[pxUnit, emUnit]} />

                            <UnitControl className='mt20' label={__('Pagination Height:', 'content-slider-block')} labelPosition='left' value={pageHeight} onChange={val => setAttributes({ pageHeight: val })} units={[pxUnit, emUnit]} />

                            <BorderControl label={__('Pagination Border:', 'content-slider-block')} border={pageBorder} onChange={val => setAttributes({ pageBorder: val })} defaults={{ radius: '50%' }} />
                        </>}

                        {isPrevNext && <BColor label={__('Preview Next Button Color:', 'content-slider-block')} value={prevNextColor} onChange={val => setAttributes({ prevNextColor: val })} defaultColor='#fff' />}
                    </PanelBody> : ''}


                    {/* Slide Title */}
                    <PanelBody className='bPlPanelBody' title={__('Slide Title', 'content-slider-block')} initialOpen={false}>
                        <ToggleControl label={__('Show Title', 'content-slider-block')} checked={isTitle} onChange={val => setAttributes({ isTitle: val })} />

                        {isTitle && <Typography typography={titleTypo} onChange={val => setAttributes({ titleTypo: val })} defaults={{ fontSize: 25 }} />}
                    </PanelBody>


                    {/* Slide Description */}
                    <PanelBody className='bPlPanelBody' title={__('Slide Description', 'content-slider-block')} initialOpen={false}>
                        <ToggleControl label={__('Show Description', 'content-slider-block')} checked={isDesc} onChange={val => setAttributes({ isDesc: val })} />

                        {isDesc && <Typography typography={descTypo} onChange={val => setAttributes({ descTypo: val })} defaults={{ fontSize: 15 }} />}
                    </PanelBody>


                    {/* Slide Button */}
                    <PanelBody className='bPlPanelBody' title={__('Slide Button', 'content-slider-block')} initialOpen={false}>
                        <ToggleControl label={__('Show Button', 'content-slider-block')} checked={isBtn} onChange={val => setAttributes({ isBtn: val })} />

                        {isBtn && <>
                            <ToggleControl label={__('Open link in new tab', 'content-slider-block')} checked={'_blank' === linkTarget ? true : false} onChange={val => setAttributes({ linkTarget: val ? '_blank' : '' })} />

                            <Typography typography={btnTypo} onChange={val => setAttributes({ btnTypo: val })} defaults={{ fontSize: 16 }} />

                            <SpaceControl className='mt20' label={__('Padding:', 'content-slider-block')} space={btnPadding} onChange={val => setAttributes({ btnPadding: val })} defaults={{ vertical: '12px', horizontal: '35px' }} />

                            <BorderControl label={__('Border:', 'content-slider-block')} border={btnBorder} onChange={val => setAttributes({ btnBorder: val })} defaults={{ radius: '3px' }} />
                        </>}
                    </PanelBody>
                </>}
            </>}</TabPanel>
        </InspectorControls>

        <BlockControls>
            <ToolbarGroup className='bPlToolbar'>
                <ToolbarButton label={__('Add New Slide', 'content-slider-block')} onClick={addSlide} ><Dashicon icon='plus' size={23} /></ToolbarButton>
            </ToolbarGroup>

            <AlignmentToolbar value={sliderAlign} onChange={val => setAttributes({ sliderAlign: val })} describedBy={__('Slider Alignment')} alignmentControls={[
                { title: __('Slider in left', 'content-slider-block'), align: 'left', icon: 'align-left' },
                { title: __('Slider in center', 'content-slider-block'), align: 'center', icon: 'align-center' },
                { title: __('Slider in right', 'content-slider-block'), align: 'right', icon: 'align-right' }
            ]} />
        </BlockControls>
    </>;
};
export default Settings;