import { withSelect } from '@wordpress/data';
import { useEffect, useState, createContext, useRef } from '@wordpress/element';

import Settings from './settings';

const $ = jQuery;

export const IndexContext = createContext();

const Edit = props => {
    const { className, attributes: { align, slides = [], columns, columnGap, sliderWidth, sliderHeight, isLoop, isAutoplay, speed, effect, isTouchMove, isPage, isPageClickable, isPageDynamic, pageColor, pageWidth, pageHeight, pageBorder, isPrevNext, prevNextColor, sliderAlign, isTitle, titleTypo, titleColor, isDesc, descTypo, descColor, isBtn, btnTypo, btnColors, btnHovColors, btnPadding, btnBorder }, setAttributes, clientId, isEditorSidebarOpened } = props;

    useEffect(() => { clientId && setAttributes({ cId: clientId }); }, [clientId]); // Set & Update clientId to cId

    const [activeIndex, setActiveIndex] = useState(0);
    const sliderRef = useRef(null);
    const sliderWrapperRef = useRef(null);

    // const thisBlock = document.querySelector(`#block-${clientId}`);
    // useEffect(() => {
    //     const editorBlockListLayoutWidth = document.querySelector('.block-editor-block-list__layout')?.clientWidth;

    //     const editorContentAreaWidth = isEditorSidebarOpened ? window.innerWidth - 280 : window.innerWidth;

    //     const halfBlockSideSpace = (editorContentAreaWidth - editorBlockListLayoutWidth) / 2;

    //     if (thisBlock) {
    //         thisBlock.style.width = 'inherit';
    //         thisBlock.style.maxWidth = 'inherit';
    //         thisBlock.style.marginLeft = 'inherit';
    //     }
    //     if (thisBlock && align === 'wide') {
    //         thisBlock.style.width = `${editorBlockListLayoutWidth + halfBlockSideSpace}px`;
    //         thisBlock.style.maxWidth = `${editorBlockListLayoutWidth + halfBlockSideSpace}px`;
    //         thisBlock.style.setProperty('margin-left', `-${halfBlockSideSpace / 2}px`, 'important');
    //     }
    //     if (thisBlock && align === 'full') {
    //         thisBlock.style.width = `${editorContentAreaWidth - 35}px`;
    //         thisBlock.style.maxWidth = `${editorContentAreaWidth - 35}px`;
    //         thisBlock.style.setProperty('margin-left', `-${halfBlockSideSpace - (35 / 2)}px`, 'important');
    //     }
    // }, [isEditorSidebarOpened, thisBlock, align]);

    // Swiper Scripts
    const currentBlock = document.querySelector(`#block-${clientId} .wp-block`);
    useEffect(() => {
        const currentSlider = document.querySelector(`#csbContentSlider-${clientId} .csbSliderWrapper .csbContentSlider`);

        const currentAll = currentSlider && currentBlock;

        currentAll ? currentBlock.style.display = 'block' : 'initial';
        align === 'full' && currentAll ? currentBlock.style.maxWidth = 'none' : '';
        align === 'wide' && currentAll ? currentBlock.style.maxWidth = 'none' : '';
        align === '' && currentAll ? currentBlock.style.maxWidth = '' : '';

        currentAll && isEditorSidebarOpened ? currentBlock.style.display = 'grid' : '';

        currentAll ? currentSlider.style.width = `${currentBlock.clientWidth}px` : '';

        // Re init slider
        sliderWrapperRef.current.innerHTML = '';
        sliderWrapperRef.current.innerHTML = sliderRef.current.outerHTML;

        new Swiper(`#csbContentSlider-${clientId} .csbSliderWrapper .csbContentSlider`, {
            // Optional parameters
            direction: 'horizontal',
            initialSlide: activeIndex,
            slidesPerView: columns?.mobile,
            breakpoints: {
                // when window width is >= 576px
                576: { slidesPerView: columns?.tablet },
                // when window width is >= 768px
                768: { slidesPerView: columns?.desktop },
            },
            spaceBetween: columnGap,
            loop: isLoop,
            allowTouchMove: isTouchMove,
            grabCursor: isTouchMove,
            autoplay: false,
            // autoplay: isAutoplay ? { delay: speed * 1000 } : false,
            speed: speed * 1000,
            effect,
            fadeEffect: { crossFade: true },
            creativeEffect: {
                prev: {
                    shadow: true,
                    translate: ['-120%', 0, -500],
                },
                next: {
                    shadow: true,
                    translate: ['120%', 0, -500],
                }
            },
            allowSlideNext: true,
            allowSlidePrev: true,
            autoHeight: false,
            notificationClass: null,

            // Controllers
            pagination: {
                el: '.swiper-pagination',
                clickable: isPageClickable,
                dynamicBullets: isPageDynamic
            },
            navigation: {
                nextEl: '.swiper-button-next',
                prevEl: '.swiper-button-prev',
            }
        });

        let seen = {};
        $(`#csbContentSlider-${clientId} .swiper-notification`).each(function () {
            let txt = $(this).attr('class');
            if (seen[txt])
                $(this).remove();
            else
                seen[txt] = true;
        });
    }, [align, slides, columns, columnGap, isEditorSidebarOpened, currentBlock, isLoop, isTouchMove, isAutoplay, speed, effect, isPage, isPageClickable, isPageDynamic, isPrevNext, isTitle, isDesc, isBtn]);

    // Attributes change
    const updateSlide = (index, type, val, otherIndex = false, otherType = false) => {
        const newSlides = [...slides];

        if (false !== otherIndex && otherType) {
            newSlides[index]['childPositions'] = newSlides[index]['childPositions'] || [{ left: 5.8, top: 11 }, { left: 5.8, top: 22 }, { left: 5.8, top: 33 }];

            newSlides[index][type][otherIndex][otherType] = val;
            setAttributes({ slides: newSlides });
        } else {
            newSlides[index][type] = val;
            setAttributes({ slides: newSlides });
        }
        setActiveIndex(index);
    }
    // for (let i = 0; i < slides.length; i++) {
    //     titleColor && updateSlide(i, 'titleColor', titleColor);
    //     descColor && updateSlide(i, 'descColor', descColor);
    //     btnColors && updateSlide(i, 'btnColors', btnColors);
    //     btnHovColors && updateSlide(i, 'btnHovColors', btnHovColors);
    // } // For Update 1.2.0

    // 3.0.0 v fixing start
    $(document).ready(function () {
        const calcPercentage = (child, parent) => 100 * parseFloat(child / parent);
        const elWidth = el => parseInt(el?.clientWidth);
        const elHeight = el => parseInt(el?.clientHeight);

        slides.map((item, index) => {
            const { position } = item;

            const sliderContent = document.querySelector(`#csbContentSlider-${clientId} .csbContentSlider .slide-${index}:not(.swiper-slide-duplicate) .sliderContent`);
            const contentChild = sliderContent?.children || [];

            const newSlides = [...slides];
            newSlides[index]['childPositions'] = newSlides[index]['childPositions'] || [{ left: 5.8, top: 11 }, { left: 5.8, top: 22 }, { left: 5.8, top: 33 }];

            const resetPosition = () => {
                newSlides[index]['position'] = '';
                setAttributes({ slides: newSlides });
            }
            const childPositionsForOld = (i, top, left) => {
                newSlides[index]['childPositions'][i]['top'] = top;
                newSlides[index]['childPositions'][i]['left'] = left;
            }

            const revI = [...Array(contentChild?.length).keys()].reverse();
            const middleI = Math.ceil(contentChild?.length / 2 - 1);
            const beforeMiddleRevI = [...Array(middleI).keys()].reverse();
            for (let i = 0; i < contentChild?.length; i++) {
                const contentChildWidthPer = calcPercentage(elWidth(contentChild[i]), elWidth(sliderContent));
                const contentChildHeightPer = calcPercentage(elHeight(contentChild[i]), elHeight(sliderContent));

                if (0 < contentChildWidthPer && 0 < contentChildHeightPer && isFinite(contentChildWidthPer) && isFinite(contentChildHeightPer)) {
                    const verticalLeft = 5.8;
                    const verticalCenter = (50 - (contentChildWidthPer / 2));
                    const verticalRight = (100 - contentChildWidthPer - verticalLeft);

                    const horizontalTop = ((i + 1) * 11);
                    const horizontalCenterQuery = i === middleI ? 50 : i < middleI ? 50 - (11 * (beforeMiddleRevI[i] + 1)) : 50 + (11 * (i - middleI));
                    const horizontalCenter = (horizontalCenterQuery - (contentChildHeightPer / 2));
                    const horizontalBottom = (100 - (11 * (i + 1)) - contentChildHeightPer);

                    if ('top left' === position) {
                        childPositionsForOld(i, horizontalTop, verticalLeft);
                        i + 1 === contentChild?.length && resetPosition();
                    } else if ('top center' === position) {
                        childPositionsForOld(i, horizontalTop, verticalCenter);
                        i + 1 === contentChild?.length && resetPosition();
                    } else if ('top right' === position) {
                        childPositionsForOld(i, horizontalTop, verticalRight);
                        i + 1 === contentChild?.length && resetPosition();
                    } else if ('center left' === position) {
                        childPositionsForOld(i, horizontalCenter, verticalLeft);
                        i + 1 === contentChild?.length && resetPosition();
                    } else if ('center center' === position) {
                        childPositionsForOld(i, horizontalCenter, verticalCenter);
                        i + 1 === contentChild?.length && resetPosition();
                    } else if ('center right' === position) {
                        childPositionsForOld(i, horizontalCenter, verticalRight);
                        i + 1 === contentChild?.length && resetPosition();
                    } else if ('bottom left' === position) {
                        childPositionsForOld(revI[i], horizontalBottom, verticalLeft);
                        0 === revI[i] && resetPosition();
                    } else if ('bottom center' === position) {
                        childPositionsForOld(revI[i], horizontalBottom, verticalCenter);
                        0 === revI[i] && resetPosition();
                    } else if ('bottom right' === position) {
                        childPositionsForOld(revI[i], horizontalBottom, verticalRight);
                        0 === revI[i] && resetPosition();
                    }
                }
                // 3.0.0 v fixing end

                $(contentChild[i]).draggable({
                    containment: 'parent',
                    cursor: 'dragging',
                    stop() {
                        const top = calcPercentage($(this).position().top, elHeight(sliderContent));
                        const left = calcPercentage($(this).position().left, elWidth(sliderContent));
                        updateSlide(index, 'childPositions', top.toFixed(3), i, 'top');
                        updateSlide(index, 'childPositions', left.toFixed(3), i, 'left');
                    }
                });
            }
        });
    });

    return <>
        <IndexContext.Provider value={setActiveIndex}>
            <Settings attributes={props.attributes} setAttributes={setAttributes} />
        </IndexContext.Provider>

        <div className={className} id={`csbContentSlider-${clientId}`}>
            <style dangerouslySetInnerHTML={{
                __html: `
                @import url(${titleTypo?.googleFontLink});
                @import url(${descTypo?.googleFontLink});
                @import url(${btnTypo?.googleFontLink});

                #csbContentSlider-${clientId} .csbSliderWrapper{
                    text-align: ${sliderAlign};
                }
                #csbContentSlider-${clientId} .csbContentSlider{
                    width: ${sliderWidth};
                    height: ${sliderHeight};
                }
                #csbContentSlider-${clientId} .csbContentSlider .slide{ height: ${sliderHeight}; }
                
                #csbContentSlider-${clientId} .csbContentSlider .slide .sliderTitle{
                    ${titleTypo?.styles || 'font-size: 25px;'}
                }
                #csbContentSlider-${clientId} .csbContentSlider .slide .sliderDescription{
                    ${descTypo?.styles || 'font-size: 15px;'}
                }
                #csbContentSlider-${clientId} .csbContentSlider .slide .sliderBtn{
                    ${btnTypo?.styles || 'font-size: 16px;'}
                    padding: ${btnPadding?.styles || '12px 35px'};
                    ${btnBorder?.styles || 'border-radius: 3px;'}
                }

                #csbContentSlider-${clientId} .csbContentSlider .swiper-pagination .swiper-pagination-bullet{
                    background: ${pageColor};
                    width: ${pageWidth};
                    height: ${pageHeight};
                    ${pageBorder?.styles || 'border-radius: 50%;'}
                }
                
                #csbContentSlider-${clientId} .csbContentSlider .swiper-button-prev, #csbContentSlider-${clientId} .csbContentSlider .swiper-button-next{ color: ${prevNextColor}; }
            `}} />
            <style>
                {slides.map((item, index) => {
                    return `#csbContentSlider-${clientId} .csbContentSlider .slide-${index}{
                        ${item.background?.styles || 'background-color: #00000080;'}
                    }
                    #csbContentSlider-${clientId} .csbContentSlider .slide-${index} .sliderTitle{
                        color: ${item.titleColor || titleColor || '#fff'};
                    }
                    #csbContentSlider-${clientId} .csbContentSlider .slide-${index} .sliderDescription{
                        color: ${item.descColor || descColor || '#fff'};
                    }
                    #csbContentSlider-${clientId} .csbContentSlider .slide-${index} .sliderBtn{
                        ${item.btnColors?.styles || btnColors?.styles || 'color: #fff; background: #4527a4;'}
                    }
                    #csbContentSlider-${clientId} .csbContentSlider .slide-${index} .sliderBtn:hover{
                        ${item.btnHovColors?.styles || btnHovColors?.styles || 'color: #fff; background: #8344c5;'}
                    }`;
                })}

                {slides.map((item, index) => {
                    const { childPositions = [{ left: 5.8, top: 11 }, { left: 5.8, top: 22 }, { left: 5.8, top: 33 }] } = item;

                    const contentChild = document.querySelector(`#csbContentSlider-${clientId} .csbContentSlider .slide-${index} .sliderContent`)?.children || [];

                    let childElPositionCSS = '';
                    for (let i = 0; i < contentChild.length; i++) {
                        childPositions[i] = childPositions[i] || { left: 5.8, top: (i + 1) * 11 }

                        childElPositionCSS += `#csbContentSlider-${clientId} .csbContentSlider .slide-${index} .sliderContent > *:nth-child(${i + 1}){ top: ${childPositions[i].top}%; left: ${childPositions[i].left}%; }`;
                    }
                    return childElPositionCSS;
                })}
            </style>

            <div className='csbSliderWrapper' ref={sliderWrapperRef}></div>
            <div className='csbContentSlider' ref={sliderRef}>
                <div className='swiper-wrapper'>
                    {slides.map((item, index) => {
                        const { position, title, description, btnText } = item;

                        return <div key={index} className={`slide slide-${index} swiper-slide`}>
                            <div className={`sliderContent ${position?.split(' ')?.join('-')}`}>
                                {isTitle && title && <h2 className='sliderTitle'>{title}</h2>}
                                {isDesc && description && <p className='sliderDescription'>{description}</p>}
                                {isBtn && btnText && <a className='sliderBtn'>{btnText}</a>}
                            </div>
                        </div>;
                    })}
                </div>

                {isPage && <div className='swiper-pagination'></div>}

                {isPrevNext && <><div className='swiper-button-prev'></div><div className='swiper-button-next'></div></>}
            </div>
        </div>
    </>;
};
export default withSelect((select) => {
    return {
        isEditorSidebarOpened: select('core/edit-post').isEditorSidebarOpened()
    };
})(Edit);